.navbar {
    &-brand {
        background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/DOYOGA-logo.png);
        width: 96px;
        height: 31px;
        white-space: nowrap; /*  */
        text-indent: 101%; /*  */
        overflow: hidden; /*  */
    }
    &-toggler {
        color: #707070;
        border-color: rgba(0, 0, 0, 0);
    }
    .nav {
        &-link {
            color: #707070;
        }
        &-button {
            color: #907a7b;
            border: 1px solid #907a7b;
        }
    }
}
