body {
    font-family: "Noto Sans TC", sans-serif;
    line-height: 1.5;
}
img {
    display: block; /* 2-3px */
    max-width: 100%; /*  */
    height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    line-height: 1.2;
    margin-bottom: 0;
}

a {
    display: block;
    text-decoration: none;
    &:hover {
        color: pink;
    }
}

p {
    margin-bottom: 0;
}

ul,
ol,
li {
    list-style: none;
    padding: 0;
    margin-bottom: 0;
}

.bg-cover {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
